.ads {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ads h2 {
  padding-bottom: 55px;
}

.ads__row {
  width: 100%;
  display: flex;
  flex-grow: 1;
}

.ads__subscription,
.ads__col {
  display: flex;
}

.ads__col {
  width: 48.3%;
}

.ads__subscription {
  width: 48.3%;
  margin-right: 3.4%;
}

.ads__info {
  width: 42%;
  padding-top: 40px;
  padding-left: 8%;
  padding-bottom: 45px;

  background-color: #ed0233;
  color: #ffffff;
  flex-grow: 1;
}

.ads__info-year {
  font-weight: bold;
  font-size: 28px;
  line-height: 30px;
  margin: 0;
}

.ads__info-date {
  font-weight: bold;
  font-size: 70px;
  line-height: 70px;
  margin: 0;
  padding-bottom: 20px;
}

.ads__info-text {
  font-size: 18px;
  line-height: 140%;
  margin: 0;
  padding-bottom: 189px;
}

.ads__info a {
  text-transform: uppercase;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
}

.ads__photo {
  width: 50%;
}

.ads__photo img {
  height: 100%;
  object-fit: cover;
}
