.ads-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
}

.ads-list__item {
  padding: 0;
  padding-left: 23px;
  padding-bottom: 26px;

  color: #1c3374;
  border-width: 10px;
  border-style: solid;
  box-sizing: border-box;
  border-image-slice: 10;
  border-image-source: url("../../img/border-desktop.png");

  margin-bottom: 41px;

  &:nth-child(2) {
    margin-bottom: 0;
  }
}

.ads-list__item h3 {
  font-weight: bold;
  font-size: 26px;
  line-height: 30px;
  margin: 0;
  padding-top: 20px;
  padding-bottom: 7px;
}

.ads-list__item p {
  font-size: 16px;
  line-height: 140%;
  margin: 0;
  padding-bottom: 68px;
}

.ads-list__item a {
  text-transform: uppercase;
  color: #1c3374;
  border-bottom: 1px solid #1c3374;
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
}
